import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';
import { BikesService } from 'src/app/services/bikes.service';
import { StorageService } from 'src/app/services/storage.service';
import { FavouritesComponent } from '../favourites/favourites.component';

@Component({
  selector: 'app-map',
  templateUrl: './map.component.html',
  styleUrls: ['./map.component.scss']
})
export class MapComponent implements OnInit {
  @ViewChild(FavouritesComponent,{read: ElementRef})
  favesEl: FavouritesComponent;
  
  favesList: number[];
  location: Object = {};
  locations: [];
  selectedLocation;
  lat;
  lng;

  constructor(
    private bikes: BikesService,
    private storage: StorageService
  ) { }

  ngOnInit() {
    this.bikes.getLocations().subscribe(locations => {
      this.storage.storeLocations(locations);
    });
    this.storage.locations.subscribe(l => this.locations = l)
    if (navigator.geolocation) {
      navigator.geolocation.watchPosition(pos => {
        this.location = pos;
        this.lat = pos.coords.latitude;
        this.lng = pos.coords.longitude;
        console.log('usr location', pos);
      });
    } else {
      console.log('no navigator');
    }
    this.storage.faves.subscribe(faves => {
      this.favesList = faves;
    });
  }

  formatMarker(loc) {
    return `${loc.available_bikes.toString()} / ${loc.available_bike_stands.toString()}`
  }

  toggleFaveList() {
    // @ts-ignore
    this.favesEl.nativeElement.classList.toggle('open');
  }

  markerClick(e, location) {
    console.log(e);
    this.selectedLocation = location;
    console.log(location);

  }

  mapClick(e) {
    console.log('mapClick', e);
    this.selectedLocation = {};
  }

  mapReady(e) {
    console.log('mapReady', e);
  }

  mapIdle(e) {
    console.log('mapIdle', e);
  }

}
